import { useQuery } from '@abyss/web/hooks/useQuery';
import { config } from '@abyss/web/tools/config';
import CreateMigrateIssue from './CreateMigrateIssue.graphql';

export const useCreateMigrateIssue = (options) => {
  return useQuery(CreateMigrateIssue, {
    ...options,
    url: config('GRAPHQL_API_PATH'),
    dataKey: 'createMigrateIssue',
    initialState: {},
  });
};
